.Banner-gela{
    background-image: url(../../../public/assets/banner-sobre.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; 
    width: 100%;
    height: 90vh;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}
