.img-sabores{
    background-image: url('../../../public/assets/banner-sabores.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; 
    width: 100%;
    height: 80vh;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}
.sor{
    margin: auto;
    padding:32px ;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
}
.sorvetes{
 width: 400px;
 height: 400px;
 box-shadow: 1px 5px 7px ;
 border-radius: 10px;
 display: flex;
 flex-wrap: wrap;
 align-items: center;
align-self: center;
justify-content: center;
align-content: space-between;
}
.sorvetes:hover{
    width: 410px;
    height: 412px;
}
.sorvetes img{ 
    width: 280px;
    height: 280px;
 align-items: center;
}
.sorvetes h3{
    text-align: center;
    font-family:cursive;
    font-weight: bold;
  font-size: x-large;
}
@media (max-width:440px) {
    .iamgem-topo{
        display: none;
        
    }

    
}

